export default {
  name: 'hc-icon-btn',
  props: {
    icon: {
      type: String,
      default: 'fas fa-globe'
    },
    stretch: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    stretchClass: function stretchClass() {
      return this.stretch ? 'h-100' : null;
    }
  }
};