export default {
  name: 'hc-accordion-item',
  props: {
    /**
    * The icon class. The icon will be rotated 180 degrees when expanded
    */
    icon: {
      type: String,
      default: 'fas fa-chevron-down'
    },

    /**
    * Expanded on mount
    */
    open: {
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      expanded: false
    };
  },
  computed: {
    expandedClass: function expandedClass() {
      return this.expanded ? 'expanded' : null;
    },
    parentAttr: function parentAttr() {
      return this.$parent.multiple ? null : "#accordion".concat(this.$parent._uid);
    }
  },
  created: function created() {
    if (!this.$slots.header) console.warn('The accordion item has no header');
  },
  methods: {
    hide: function hide() {
      this.expanded = false;
      this.$refs.collapse.classList.toggle('collapsing');
      this.$refs.collapse.classList.remove('show');
      this.$refs.collapse.classList.toggle('collapsing');
    },
    show: function show() {
      this.expanded = true;
      this.$refs.collapse.classList.toggle('collapsing');
      this.$refs.collapse.classList.add('show');
      this.$refs.collapse.classList.toggle('collapsing');
    }
  },
  mounted: function mounted() {
    if (this.open) this.show();
  }
};